  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Poppins", sans-serif, system-ui, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 500;
  background-color: #0e2a47;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.screen-hidden,
.ship-hidden {
  display: none !important;
}

.disabled-btn {
  opacity: 0.4 !important;
  pointer-events: none;
}

.rotate {
  transform: rotate(-90deg) translateX(-100%);
  transform-origin: top left;
}

.shipImg {
  position: absolute;
  top: 0;
  left: 0;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  filter: invert(72%) sepia(57%) saturate(7189%) hue-rotate(172deg)
    brightness(101%) contrast(104%);
  z-index: 0;
}

.enemy-ship {
  filter: invert(51%) sepia(82%) saturate(505%) hue-rotate(345deg)
    brightness(92%) contrast(106%);
}

.game-intro-section,
.ship-placement-section,
.battle-section,
.gameover-section {
  height: 100dvh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.game-intro-section,
.gameover-section {
  position: relative;
}

.game-logo {
  position: absolute;
  top: 10%;
  left: 0;
  width: 100%;
  height: 50px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.player-name-container,
.ship-placement-container,
.gameover-container,
.profile-link {
  width: 95%;
  max-width: 480px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.75);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.6);
}

.profile-link {
  max-width: 100px;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  margin-top: 20px;
}

ion-icon {
  display: flex;
  align-items: center;
  font-size: 1.9rem;
  color: white;
}

.player-name-container label {
  padding: 10px 0;
}

.player-name-container input[type="text"] {
  padding: 7px;
  font-family: "Poppins";
  font-weight: 600;
  font-size: 1.2rem;
  width: 90%;
  text-align: center;
  border-radius: 4px;
  color: #ffffff;
  outline: none;
  border: none;
  border-bottom: 1px solid #ffffff;
  background-color: transparent;
}

.confirm-btn,
.orientation-btn,
.random-btn,
.reset-btn,
.proceed-btn,
.playagain-btn {
  margin: 10px 0;
  padding: 4px 20px;
  font-family: "Poppins";
  font-size: 1.2rem;
  font-weight: 600;
  border-radius: 5px;
  border: 2px solid #ffffff;
  background-color: transparent;
  color: #ffffff;
  transition: background-color 0.4s ease-in-out;
  transition: color 0.4s ease-in-out;
}

.ship-placement-container {
  max-width: 550px;
}

.player-board {
  width: 95%;
  display: grid;
  grid-template-rows: repeat(10, 1fr);
  background: rgb(2, 43, 67);
  background: radial-gradient(
    circle,
    rgba(2, 43, 67, 0.5) 0%,
    rgba(3, 66, 103, 0.5) 50%,
    rgba(0, 119, 173, 0.5) 100%
  );
  pointer-events: none;
}

.setup-board {
  pointer-events: auto;
}

.board-row {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
}

.board-col {
  position: relative;
  border: 1px solid #57859d;
  aspect-ratio: 1/1;
}

.valid-hover {
  background-color: #86efac;
}

.invalid-hover {
  background-color: #f43f5e;
}

.cell-hit,
.cell-miss {
  pointer-events: none;
}

.current-vessel {
  margin: 0;
  padding: 5px;
  font-size: 1.4rem;
  font-weight: 600;
  text-transform: uppercase;
}

.vessel {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  filter: invert(72%) sepia(57%) saturate(7189%) hue-rotate(172deg)
    brightness(101%) contrast(104%);
}

.ship-actions,
.placement-actions {
  width: 70%;
  display: flex;
  justify-content: space-between;
  gap: 5px;
}

.vessel-container,
.placement-actions {
  padding: 15px 0;
}

.gameboard-container {
  width: 95%;
  max-width: 480px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  font-size: 1.8rem;
  color: #ffffff;
}

.board-panel {
  width: 60%;
  display: grid;
  grid-template-rows: max-content max-content;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.75);
  font-size: 0.8rem;
  padding: 10px;
  transition: width 0.5s ease-in-out;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.6);
}

.panel-name {
  margin: 0;
  font-weight: 600;
}

.player-two-panel .player-board {
  background: rgb(67, 35, 2);
  background: radial-gradient(
    circle,
    rgba(67, 35, 2, 0.5) 0%,
    rgba(103, 51, 3, 0.5) 50%,
    rgba(173, 89, 0, 0.5) 100%
  );
  cursor: crosshair;
}

.player-two-panel .player-board .board-col {
  border-color: #8f735b;
}

.player-one-panel > .panel-name {
  justify-self: flex-start;
  color: #e0f2fe;
}

.player-two-panel > .panel-name {
  justify-self: flex-end;
  color: #ffedd5;
}

.board-panel > .player-board {
  margin-top: 10px;
  width: 100%;
}

.board-focus {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

.board-focus .player-board {
  pointer-events: auto;
}

.player-one-panel {
  align-self: self-start;
}

.player-two-panel {
  align-self: self-end;
}

.winner-announcement {
  margin: 0;
  padding: 15px;
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
}

.gameover-phrase {
  max-width: 80%;
  padding: 15px;
  margin: 0;
  text-align: center;
  text-wrap: balance;
  font-size: 1rem;
}

.playagain-btn {
  margin: 15px;
}

ion-icon:hover {
  scale: 1.15;
  transition: scale 0.3s ease-in-out;
}

.confirm-btn:hover,
.orientation-btn:hover,
.random-btn:hover,
.reset-btn:hover,
.proceed-btn:hover,
.playagain-btn:hover {
  color: rgba(0, 0, 0, 0.75);
  background-color: #ffffff;
  border-color: rgba(0, 0, 0, 0.75);
}

.cell-hit::after,
.cell-miss::after {
  position: absolute;
  content: "";
  top: 32%;
  left: 32%;
  z-index: 20;
  width: 33%;
  height: 33%;
  border-radius: 100%;
  background-color: #dc2626;
}

.cell-miss::after {
  background-color: #cbd5e1;
}

@media (min-width: 800px) {
  .game-logo {
    height: 80px;
  }

  .gameboard-container {
    max-width: 1400px;
    flex-direction: row;
    justify-content: space-between;
    gap: 100px;
  }

  .board-focus {
    width: 100%;
  }

  .board-panel {
    width: 40%;
    padding: 15px;
  }

  .player-board {
    opacity: 0.7;
    transition: opacity 0.5s ease-in-out;
  }

  .board-focus .player-board {
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
  }

  .panel-name {
    font-size: 1.2rem;
  }

  .winner-announcement {
    font-size: 1.6rem;
  }

  .gameover-phrase {
    font-size: 1.1rem;
  }

  .vessel-container:hover {
    scale: 1.05;
  }
}
